@font-face {
 font-family: "MyriadProRegular";
 src: url("../fonts/MyriadProRegular/MyriadProRegular.eot");
 src: url("../fonts/MyriadProRegular/MyriadProRegular.eot?#iefix")format("embedded-opentype"),
 url("../fonts/MyriadProRegular/MyriadProRegular.woff") format("woff"),
 url("../fonts/MyriadProRegular/MyriadProRegular.ttf") format("truetype") {}
 font-style: normal;
 font-weight: normal; }

img {
	max-width: 100%; }

body {
	font-family: "MyriadProRegular"; }


.container {
	border: 1px solid red;
	max-width: 1114px;
	padding: 308px 0 0 0;
	min-height: 1290px;
	background-image: url(../img/form-reg/form-reg-bg.jpg);
 background-repeat: no-repeat;
	background-size: cover;
	background-position: center; }


.form {
	&-title {
	 font-size: 28px;
	 color: #b0b0b0;
	 margin-bottom: 60px; }
	&-reg {
	 border: 1px solid red;
	 margin-right: -15px;
	 &-header {
	  background-color: #34b7ce;
	  padding: 22px 0;
	  border-radius: 10px 10px 0 0;
	  justify-content: center;
	  border: 1px solid red; }
	 &__logo {
	  text-align: center; }
	 &__title {
   font-size: 33px;
	  color: #fff;
	  margin-left: -29px; }
	 &__input {
	  display: block;
	  width: 100%;
	  border-radius: 10px;
	  border: 1px solid #dcdcdc;
	  padding: 20px 20px 26px 55px;
	  margin-bottom: 33px; }
  &-body {
   padding-bottom: 32px;
	  background-color: #f5f5f5;
	  input {
		  &::-webkit-input-placeholder {
				 font-size: 22px;
				 color: #ececec; }
			 &::-moz-placeholder {
				 font-size: 22px;
				 color: #ececec; }
				&:-ms-input-placeholder {
				 font-size: 22px;
				 color: #ececec; }
				&:-moz-placeholder {
				 font-size: 22px;
				 color: #ececec; } } } }
 &-login {
  padding: 30px 28px 0;
  border-right: 1px solid #e7e7e7;
  &__image {
   margin-top: 45px; } }
 &-newreg {
  &__title {
   padding-top: 34px;
   font-size: 20px;
   color: #9b9b9b; } } }











